<template>
  <div id="about" style="margin-top:30px">
    <v-toolbar flat>
      <v-toolbar-title>
        <v-card-title>
          <span
            style="text-shadow: 1px 1px 1px black; margin-

right: 5px"
          >
            <v-avatar color="secondary">
              <v-icon dark>mdi-google-my-business</v-icon>
            </v-avatar>
            QUIENES SOMOS
          </span>
        </v-card-title>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-card-text class="my-4 text-justify title">
<v-row align="center" justify="center">
  <v-col cols="12" lg="8">
                    <v-card-text class="text--primary text-justify">
                  <div align-center>{{ summary }}</div>
                </v-card-text>
  </v-col>
</v-row>
    </v-card-text>
    <v-card-text class="">
      <v-hover v-slot:default="{ hover }" close-delay="200">
        <v-card :elevation="hover ? 16 : 2" class="mx-auto" max-width="350">
          <img
            :src="require('@/assets/images/quienes-somos1.jpg')"
            contain
            max-width="100%"
            width="100%"
          />
        </v-card>
      </v-hover>
    </v-card-text>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      summary:""
    };
  },
  mounted() {
    this.getSummary()
    if (this.$route) this.route = this.$route.query.redirect;
  },
  beforeCreate() {
    if (this.$auth.isAuth()) {
      this.$router.push("/session");
    }
  },
  methods: {
    getSummary() {
      this.$http
        .get(`/listar-infoempresa`)
        .then((res) => {
          if (res.data.business[0].summary != null) {
            this.summary = res.data.business[0].summary;
          }
        })
        .finally();
    },

  },
};
</script>